/* eslint-disable  */

// @flow
import React, { Component } from 'react'
import GraphiQL from 'graphiql'
import copyToClipboard from 'copy-to-clipboard'

import GraphiQLExplorer from 'graphiql-explorer'
import { buildClientSchema, getIntrospectionQuery, parse } from 'graphql'
// import {isMobile} from 'react-device-detect';

import { makeDefaultArg, getDefaultScalarArgValue } from './CustomArgs'

import 'graphiql/graphiql.css'
import './App.css'

import type { GraphQLSchema } from 'graphql'
import { isMobile } from 'react-device-detect'

function fetcher(params: Object): Object {
  return fetch('/graphql/', {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(params)
  })
    .then(function (response) {
      return response.text()
    })
    .then(function (responseBody) {
      try {
        return JSON.parse(responseBody)
      } catch (e) {
        return responseBody
      }
    })
}

const DEFAULT_QUERY = `# shift-option/alt-click on a query below to jump to it in the explorer
# option/alt-click on a field in the explorer to select all subfields
{
  ThetaRpc {
    GetStatus {
      current_height
      address
      genesis_block_hash
    }
  }
  StakeStatistics {
    theta_stake_ratio
    total_validator_node_number
  }
  SmartContractStatistics {
    CallRank(rank_by: last_24h_call_times) {
      last_24h_call_times
      contract_address
    }
  }
}
`

type State = {
  schema: ?GraphQLSchema,
  query: string,
  explorerIsOpen: boolean
}

class Playground extends Component<{}, State> {
  _graphiql: GraphiQL
  state = { schema: null, query: DEFAULT_QUERY, explorerIsOpen: !isMobile }

  componentDidMount() {
    fetcher({
      query: getIntrospectionQuery()
    }).then((result) => {
      const editor = this._graphiql.getQueryEditor()
      editor.setOption('extraKeys', {
        ...(editor.options.extraKeys || {}),
        'Shift-Alt-LeftClick': this._handleInspectOperation
      })

      this.setState({ schema: buildClientSchema(result.data) })
    })
  }

  _handleInspectOperation = (cm: any, mousePos: { line: Number, ch: Number }) => {
    const parsedQuery = parse(this.state.query || '')

    if (!parsedQuery) {
      console.error("Couldn't parse query document")
      return null
    }

    var token = cm.getTokenAt(mousePos)
    var start = { line: mousePos.line, ch: token.start }
    var end = { line: mousePos.line, ch: token.end }
    var relevantMousePos = {
      start: cm.indexFromPos(start),
      end: cm.indexFromPos(end)
    }

    var position = relevantMousePos

    var def = parsedQuery.definitions.find((definition) => {
      if (!definition.loc) {
        console.log('Missing location information for definition')
        return false
      }

      const { start, end } = definition.loc
      return start <= position.start && end >= position.end
    })

    if (!def) {
      console.error('Unable to find definition corresponding to mouse position')
      return null
    }

    var operationKind =
      def.kind === 'OperationDefinition'
        ? def.operation
        : def.kind === 'FragmentDefinition'
        ? 'fragment'
        : 'unknown'

    var operationName =
      def.kind === 'OperationDefinition' && !!def.name
        ? def.name.value
        : def.kind === 'FragmentDefinition' && !!def.name
        ? def.name.value
        : 'unknown'

    var selector = `.graphiql-explorer-root #${operationKind}-${operationName}`

    var el = document.querySelector(selector)
    el && el.scrollIntoView()
  }

  _handleEditQuery = (query: string): void => this.setState({ query })

  _handleToggleExplorer = () => {
    this.setState({ explorerIsOpen: !this.state.explorerIsOpen })
  }

  render() {
    const { query, schema } = this.state
    return (
      <div className='graphiql-container'>
        <GraphiQLExplorer
          schema={schema}
          query={query}
          onEdit={this._handleEditQuery}
          onRunOperation={(operationName) => this._graphiql.handleRunQuery(operationName)}
          explorerIsOpen={this.state.explorerIsOpen}
          onToggleExplorer={this._handleToggleExplorer}
          getDefaultScalarArgValue={getDefaultScalarArgValue}
          makeDefaultArg={makeDefaultArg}
        />
        <GraphiQL
          ref={(ref) => (this._graphiql = ref)}
          fetcher={fetcher}
          docExplorerOpen={false}
          schema={schema}
          query={query}
          onEditQuery={this._handleEditQuery}
        >
          <GraphiQL.Logo>Graphql</GraphiQL.Logo>
          <GraphiQL.Toolbar>
            <GraphiQL.Button
              onClick={this._handleToggleExplorer}
              label='Explorer'
              title='Toggle Explorer'
            />
            {/*<GraphiQL.Button*/}
            {/*  onClick={() => this._graphiql.handlePrettifyQuery()}*/}
            {/*  label='Prettify'*/}
            {/*  title='Prettify Query (Shift-Ctrl-P)'*/}
            {/*/>*/}
            <GraphiQL.Button
              onClick={() => this._graphiql.handleToggleHistory()}
              label='History'
              title='Show History'
            />
            {!isMobile && (
              <GraphiQL.Button
                onClick={() => copyToClipboard(query)}
                label='Copy Operation'
                title='Copy Operation'
              />
            )}
          </GraphiQL.Toolbar>
        </GraphiQL>
      </div>
    )
  }
}

export default Playground
